.modal {
    position: absolute;
    top: 100px;
    right: 50%;
    transform: translate(50%);
    z-index: 999;
    opacity: 0;
    transition: all 0.3s ease-in-out;
    pointer-events: none;
    &__body {
        background-color: var(--white-bg);
        padding: 24px;
        box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.2);
        border-radius: 16px;
    }
    &__links {
        display: flex;
        flex-direction: column;
    }
    &__link {
        display: flex;
        white-space: nowrap;
        align-items: center;
        margin-top: 12px;
        pointer-events: none;
        cursor: none;
        &:nth-child(1) {
            margin-top: 0px;
        }
        span {
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            margin-left: 8px;
            color: var(--text-paragraph);
            transition: color 0.2s ease-in-out;
        }
        &:hover {
            .modal__ico {
                path {
                    fill: var(--main-color);
                }
            }
            .modal__ico_heart {
                path {
                    stroke: var(--main-color);
                }
            }
            span {
                color: var(--text-title);
            }
        }
    }
    &__ico {
        width: 20px;
        height: 20px;
        path {
            fill: var(--text-paragraph);
            transition: fill 0.2s ease-in-out;
        }
    }
    &__ico_heart {
        path {
            fill: var(--white-bg);
            stroke: var(--text-paragraph);
            transition: fill 0.2s ease-in-out;
        }
    }
}

.active {
    top: 50px;
    opacity: 1;
    pointer-events: all;
    .modal__link {
        pointer-events: all;
        cursor: pointer;
    }
}